import React from 'react';
import styled from 'styled-components';

interface initialProps  {
    image_url: string;
};

const Image = styled.img`
    width: 80px;
    border: 0px;
`;

function imageBox({image_url} : initialProps) {
    return (
        <>
            <Image src={image_url}/>
        </>
    );
}

export default imageBox;